import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Stakeholders extends Component {
  render() {
    return (
      <div>
        <div className="skew"></div>
        <section className='stakeholders'>
          <h1>Faça parte da nova geração de academias.</h1>
          <div onMouseEnter={this.mouseEnter} onMouseLeave={this.mouseLeave} className="cards">
            <div className="article-image article-image-1">
              <article id="article-info">
                <h3>Usuários</h3>
                <div className="description">
                  <h4>Entre para o Clube!</h4>
                  <p>Aproveite agora as vantagens de usar Ufit na Academia, use a tecnologia ao seu favor para alcançar suas metas!</p>
                  <a href="/convite">Pré-Cadastro</a>
                  <a className="right" href="#slide">Como Funciona</a>
                </div>
              </article>
            </div>
          </div>
          <div className="cards">
            <div className="article-image article-image-2">
              <article id="article-info">
                <h3>Academias</h3>
                <div className="description">
                  <h4>Indique sua academia!</h4>
                  <p>Apareça na nossa lista, divulgue suas atividades e ganhe agilidade e praticidade no gerenciamento dos seus clientes.</p>
                  <a href="/academias">Saiba Mais</a>
                </div>
              </article>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default Stakeholders;


