import React, { Component } from 'react'
import { Link } from 'react-router-dom';
// import Scroll from 'react-scrollchor';

class Navigation extends Component {
  render() {
    return (
      <nav role="custom-dropdown">
        <div className="group-buttons">
          <a className="button button-action" href='/convite'><span>Pré-cadastro</span></a>
        </div>
        <input type="checkbox" id="button" />
        <label htmlFor="button" onclick />
        <ul>
          <li><a href="/" className="wide">Home</a></li>
          <li><a href="/#slide" className="wide">Como Funciona</a></li>
          <li><a href="/academias" className="wide">Para Academias</a></li>
          {/* TO DO USING SCROLL AND LINK <li><Link className="button-nav wide" to='/login'><span>Entrar</span></Link></li> */}
        </ul>
      </nav>
    );
  }
}

export default Navigation;


