import React, { Component } from 'react';

import list from '../../assets/images/waitList/list.svg';
import form from '../../assets/images/waitList/form.svg';
import mail from '../../assets/images/waitList/mail.svg';
import share from '../../assets/images/waitList/share.svg';

class WaitList extends Component {
  render() {
    return (
      <div className="wait-list">
        <article>
          <img src={list} alt="list"/>
          <div>
            <p>Você faz o pré-cadastro.</p>
          </div>
        </article>
        <article>
          <div>
            <p>Seu pedido passa por uma análise</p>
          </div>
          <img src={form} alt="form"/>
        </article>
        <article>
          <img src={mail} alt="form"/>
          <div>
            <p>Se tivermos uma academia parceira próxima a você imediatamente enviamos um e-mail para que seu cadastro possa ser completado.</p>
          </div>
        </article>
        <article>
          <div>
            <p>Portanto, quantos mais amigos você apresentar a Ufit podemos procurar mais rapidamente academias na sua localidade.</p>
          </div>
          <img src={share} alt="form"/>
        </article>
      </div>
    );
  }
}

export default WaitList;


