import React, { Component } from 'react';

class Intro extends Component {
  render() {
    return (
      <section id="intro" className='intro'>
        <div className='description'>
          <h1>Academias para você!</h1>
          <p>Está surgindo um App multiplataforma que conectará pessoas que querem malhar com as academias de uma foma super prática e motivadora.</p>
        </div>
      </section>
    );
  }
}

export default Intro;



