import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom'

import AlertMessage from './views/alertMessage'

import Home from './views/home'
import Login from './views/login'
import Invite from './views/invite'
import Academias from './views/academias'
import Lista from './views/cadastro/waitList'

class Main extends Component {
  render() {
    return (
      <main id="top">
        <AlertMessage title="Atenção!" description=" Estamos contruindo as nossas aplicações, mas você já pode pedir o seu convite."/>
        <Switch>
          <Route exact path='/' component={Home}/>
          <Route exact path='/login' component={Login}/>
          <Route exact path='/lista' component={Lista}/>
          <Route exact path='/academias' component={Academias}/>
          <Route exact path='/convite' component={Invite}/>
        </Switch>
      </main>
    );
  }
}
export default Main


