import React, { Component } from 'react';

import hard from '../assets/images/services/hard.svg';
import motivation from '../assets/images/services/motivation.svg';
import travel from '../assets/images/services/travel.svg';
import site from '../assets/images/services/site.svg';

class Academias extends Component {
  render() {
    return (
      <div className="wait-list">
        <article>
          <img src={site} alt="list"/>
          <div>
            <p>Divulgue sua academia gratuitamente no nosso portal, mostre suas aulas, seus planos.</p>
          </div>
        </article>
        <article>
          <div>
            <p>Conquiste alunos de qualquer país, potencialize seu faturamento com viajantes.</p>
          </div>
          <img src={travel} alt="form"/>
        </article>
        <article>
          <img src={motivation} alt="form"/>
          <div>
            <p>Mantenha seus alunos motivados!<br/> 45% dos jovens desistem da academia nos primeiros meses. Recomende ao aluno o nosso sistema de gamificação que atribui recompensas por tarefas concluídas</p>
          </div>
        </article>
        <article>
          <div>
            <p>Dificuldades com contabilidade?<br/> Não se preocupe, podemos organizar isso para você!</p>
          </div>
          <img src={hard} alt="form"/>
        </article>
      </div>
    );
  }
}

export default Academias;


